"use client"
import React, { useState, useEffect, useRef } from 'react';
import { Play, Pause, Volume2, Maximize2, Plus, Edit2, Trash2, X, Check, AlertCircle } from 'lucide-react';

export default function LivestreamApp() {
  const [streamUrl, setStreamUrl] = useState('');
  const [isStreaming, setIsStreaming] = useState(false);
  const [overlays, setOverlays] = useState([]);
  const [activeOverlays, setActiveOverlays] = useState(new Set());
  const [showEditor, setShowEditor] = useState(false);
  const [editingOverlay, setEditingOverlay] = useState(null);
  const [isPlaying, setIsPlaying] = useState(false);
  const [volume, setVolume] = useState(100);
  const [currentTime, setCurrentTime] = useState(0);
  const [duration, setDuration] = useState(0);
  const [streamError, setStreamError] = useState('');
  const videoRef = useRef(null);
  const containerRef = useRef(null);
  const playPromiseRef = useRef(null);
  const hlsRef = useRef(null);

  // Sample overlays for demo
  useEffect(() => {
    const sampleOverlays = [
      {
        _id: '1',
        name: 'Live Badge',
        type: 'text',
        content: '🔴 LIVE',
        position: { x: 20, y: 20 },
        size: { width: 100, height: 40 },
        zIndex: 2,
        opacity: 0.9
      },
      {
        _id: '2',
        name: 'Logo',
        type: 'shape',
        content: '#FF6B6B',
        position: { x: 20, y: 70 },
        size: { width: 80, height: 60 },
        zIndex: 1,
        opacity: 0.7
      }
    ];
    setOverlays(sampleOverlays);
  }, []);

  // Handle volume changes
  useEffect(() => {
    if (videoRef.current) {
      videoRef.current.volume = volume / 100;
    }
  }, [volume]);

  // Load HLS.js dynamically
  useEffect(() => {
    const script = document.createElement('script');
    script.src = 'https://cdn.jsdelivr.net/npm/hls.js@latest';
    script.async = true;
    document.body.appendChild(script);

    return () => {
      document.body.removeChild(script);
    };
  }, []);

  // Initialize video stream
  useEffect(() => {
    if (isStreaming && videoRef.current && streamUrl) {
      const video = videoRef.current;
      setStreamError('');

      // Check if URL is HLS (.m3u8)
      if (streamUrl.includes('.m3u8')) {
        // HLS stream
        if (window.Hls && window.Hls.isSupported()) {
          if (hlsRef.current) {
            hlsRef.current.destroy();
          }

          const hls = new window.Hls({
            enableWorker: true,
            lowLatencyMode: true,
          });
          hlsRef.current = hls;

          hls.loadSource(streamUrl);
          hls.attachMedia(video);

          hls.on(window.Hls.Events.MANIFEST_PARSED, () => {
            console.log('HLS manifest loaded');
            playVideo();
          });

          hls.on(window.Hls.Events.ERROR, (event, data) => {
            console.error('HLS error:', data);
            if (data.fatal) {
              setStreamError(`HLS Error: ${data.type}`);
            }
          });
        } else if (video.canPlayType('application/vnd.apple.mpegurl')) {
          // Native HLS support (Safari)
          video.src = streamUrl;
          video.addEventListener('loadedmetadata', () => {
            playVideo();
          });
        } else {
          setStreamError('HLS not supported in this browser');
        }
      } else if (streamUrl.startsWith('rtsp://')) {
        // RTSP not supported
        setStreamError('RTSP streams require a backend conversion server. Please use HLS (.m3u8) or MP4 URLs instead.');
      } else {
        // Regular video file (MP4, WebM, etc.)
        video.src = streamUrl;
        video.addEventListener('loadeddata', () => {
          playVideo();
        });
      }
    }

    return () => {
      if (hlsRef.current) {
        hlsRef.current.destroy();
        hlsRef.current = null;
      }
    };
  }, [isStreaming, streamUrl]);

  const playVideo = () => {
    if (videoRef.current) {
      playPromiseRef.current = videoRef.current.play();
      playPromiseRef.current
        .then(() => {
          setIsPlaying(true);
          playPromiseRef.current = null;
        })
        .catch(err => {
          console.log('Autoplay prevented:', err.message);
          setIsPlaying(false);
          playPromiseRef.current = null;
        });
    }
  };

  const togglePlay = () => {
    if (!videoRef.current) return;

    if (isPlaying) {
      if (playPromiseRef.current) {
        playPromiseRef.current.then(() => {
          if (videoRef.current) {
            videoRef.current.pause();
            setIsPlaying(false);
          }
        }).catch(() => {
          setIsPlaying(false);
        });
      } else {
        videoRef.current.pause();
        setIsPlaying(false);
      }
    } else {
      playVideo();
    }
  };

  const toggleOverlay = (id) => {
    const newActive = new Set(activeOverlays);
    if (newActive.has(id)) {
      newActive.delete(id);
    } else {
      newActive.add(id);
    }
    setActiveOverlays(newActive);
  };

  const deleteOverlay = (id) => {
    setOverlays(overlays.filter(o => o._id !== id));
    const newActive = new Set(activeOverlays);
    newActive.delete(id);
    setActiveOverlays(newActive);
  };

  const handlePlayStream = () => {
    if (streamUrl.trim()) {
      setIsStreaming(true);
      setStreamError('');
    } else {
      alert('Please enter a valid stream URL');
    }
  };

  const handleSaveOverlay = (formData) => {
    if (editingOverlay) {
      setOverlays(overlays.map(o => o._id === editingOverlay._id ? { ...o, ...formData } : o));
    } else {
      const newOverlay = {
        ...formData,
        _id: Date.now().toString()
      };
      setOverlays([...overlays, newOverlay]);
    }
    setShowEditor(false);
    setEditingOverlay(null);
  };

  const formatTime = (seconds) => {
    if (!seconds || isNaN(seconds)) return '0:00';
    const h = Math.floor(seconds / 3600);
    const m = Math.floor((seconds % 3600) / 60);
    const s = Math.floor(seconds % 60);
    return h > 0 ? `${h}:${m.toString().padStart(2, '0')}:${s.toString().padStart(2, '0')}` : `${m}:${s.toString().padStart(2, '0')}`;
  };

  if (!isStreaming) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-blue-600 to-blue-800 flex flex-col">
        <header className="bg-black bg-opacity-30 text-white p-6 text-center backdrop-blur">
          <h1 className="text-4xl font-bold mb-2">🎥 Livestream Player</h1>
          <p className="text-blue-100">Watch and manage your livestream with custom overlays</p>
        </header>

        <main className="flex-1 flex items-center justify-center p-4">
          <div className="bg-white rounded-2xl shadow-2xl p-8 max-w-md w-full">
            <h2 className="text-2xl font-bold text-gray-800 mb-4">Enter Your Stream URL</h2>
            <p className="text-gray-600 mb-6">Paste your HLS (.m3u8) or video URL below</p>

            <div className="space-y-4">
              <input
                type="text"
                placeholder="https://example.com/stream.m3u8"
                value={streamUrl}
                onChange={(e) => setStreamUrl(e.target.value)}
                onKeyPress={(e) => e.key === 'Enter' && handlePlayStream()}
                className="w-full px-4 py-3 border-2 border-gray-300 rounded-lg focus:border-blue-500 focus:outline-none text-black"
              />

              <button
                onClick={handlePlayStream}
                className="w-full bg-blue-600 hover:bg-blue-700 text-white font-bold py-3 px-4 rounded-lg transition transform hover:scale-105"
              >
                ▶ Play Stream
              </button>
            </div>

            <div className="mt-8 p-4 bg-yellow-50 rounded-lg border-l-4 border-yellow-500">
              <div className="flex items-start gap-2">
                <AlertCircle className="text-yellow-600 mt-0.5 flex-shrink-0" size={20} />
                <div>
                  <h3 className="font-bold text-gray-800 mb-2">Important Note</h3>
                  <p className="text-sm text-gray-700 mb-2">
                    <strong>RTSP streams are NOT supported</strong> directly in browsers.
                  </p>
                  <p className="text-sm text-gray-700 mb-2">
                    Supported formats:
                  </p>
                  <ul className="text-xs text-gray-600 list-disc list-inside space-y-1">
                    <li>HLS streams (.m3u8)</li>
                    <li>MP4 video files</li>
                    <li>WebM video files</li>
                  </ul>
                </div>
              </div>
            </div>

            <div className="mt-4 p-4 bg-blue-50 rounded-lg border-l-4 border-blue-500">
              <h3 className="font-bold text-gray-800 mb-2">Test Stream Examples:</h3>
              <div className="space-y-2">
                <button
                  onClick={() => setStreamUrl('https://demo.unified-streaming.com/k8s/features/stable/video/tears-of-steel/tears-of-steel.ism/.m3u8')}
                  className="w-full text-left text-xs text-blue-600 hover:bg-blue-100 p-2 rounded"
                >
                  📺 Demo HLS Stream
                </button>
                <button
                  onClick={() => setStreamUrl('https://commondatastorage.googleapis.com/gtv-videos-bucket/sample/BigBuckBunny.mp4')}
                  className="w-full text-left text-xs text-blue-600 hover:bg-blue-100 p-2 rounded"
                >
                  🎬 Big Buck Bunny (MP4)
                </button>
              </div>
            </div>
          </div>
        </main>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-900">
      <header className="bg-black text-white p-4 shadow-lg">
        <h1 className="text-2xl font-bold">🎥 Livestream Player</h1>
      </header>

      <div className="grid grid-cols-1 lg:grid-cols-4 gap-6 p-6 max-w-7xl mx-auto">
        {/* Video Player Section */}
        <div className="lg:col-span-3">
          <div className="bg-black rounded-lg overflow-hidden shadow-xl">
            {streamError && (
              <div className="bg-red-600 text-white p-4 flex items-center gap-2">
                <AlertCircle size={20} />
                <span>{streamError}</span>
              </div>
            )}

            {/* Video Container */}
            <div
              ref={containerRef}
              className="relative w-full bg-black"
              style={{ aspectRatio: '16/9' }}
            >
              <video
                ref={videoRef}
                className="w-full h-full object-contain"
                playsInline
                controls={false}
                onTimeUpdate={(e) => setCurrentTime(e.target.currentTime)}
                onLoadedMetadata={(e) => setDuration(e.target.duration)}
                onPlay={() => setIsPlaying(true)}
                onPause={() => setIsPlaying(false)}
                onEnded={() => setIsPlaying(false)}
                onError={(e) => {
                  console.error('Video error:', e);
                  setStreamError('Failed to load video stream');
                }}
              />

              {/* Overlays */}
              {overlays.map(
                (overlay) =>
                  activeOverlays.has(overlay._id) && (
                    <div
                      key={overlay._id}
                      className="absolute border-2 border-dashed border-white/30 hover:border-white/70 transition pointer-events-none"
                      style={{
                        left: `${overlay.position.x}px`,
                        top: `${overlay.position.y}px`,
                        width: `${overlay.size.width}px`,
                        height: `${overlay.size.height}px`,
                        zIndex: overlay.zIndex,
                        opacity: overlay.opacity,
                      }}
                    >
                      {overlay.type === 'text' && (
                        <div className="w-full h-full flex items-center justify-center text-white font-bold text-sm bg-black/30 backdrop-blur-sm">
                          {overlay.content}
                        </div>
                      )}
                      {overlay.type === 'shape' && (
                        <div
                          className="w-full h-full"
                          style={{ backgroundColor: overlay.content }}
                        />
                      )}
                    </div>
                  )
              )}
            </div>

            {/* Controls */}
            <div className="bg-gradient-to-t from-black to-transparent p-4 space-y-3">
              {/* Progress Bar */}
              <div className="flex items-center gap-2">
                <input
                  type="range"
                  min="0"
                  max={duration || 0}
                  value={currentTime}
                  onChange={(e) => {
                    if (videoRef.current && !isNaN(e.target.value)) {
                      videoRef.current.currentTime = parseFloat(e.target.value);
                    }
                  }}
                  className="flex-1 h-1 bg-gray-700 rounded cursor-pointer accent-red-600"
                />
                <span className="text-white text-xs font-mono whitespace-nowrap">
                  {formatTime(currentTime)} / {formatTime(duration)}
                </span>
              </div>

              {/* Control Buttons */}
              <div className="flex items-center justify-between text-white">
                <div className="flex gap-2">
                  <button
                    onClick={togglePlay}
                    className="p-2 hover:bg-white/20 rounded transition"
                    title="Play/Pause"
                  >
                    {isPlaying ? <Pause size={20} /> : <Play size={20} />}
                  </button>
                </div>

                <div className="flex items-center gap-2">
                  <Volume2 size={18} />
                  <input
                    type="range"
                    min="0"
                    max="100"
                    value={volume}
                    onChange={(e) => setVolume(Number(e.target.value))}
                    className="w-20 h-1 bg-gray-700 rounded cursor-pointer accent-red-600"
                  />
                  <span className="text-xs w-8">{volume}%</span>
                </div>

                <button
                  onClick={() => {
                    if (containerRef.current) {
                      if (containerRef.current.requestFullscreen) {
                        containerRef.current.requestFullscreen();
                      } else if (containerRef.current.webkitRequestFullscreen) {
                        containerRef.current.webkitRequestFullscreen();
                      }
                    }
                  }}
                  className="p-2 hover:bg-white/20 rounded transition"
                  title="Fullscreen"
                >
                  <Maximize2 size={20} />
                </button>
              </div>
            </div>
          </div>
        </div>

        {/* Sidebar - Overlay Manager */}
        <div className="lg:col-span-1">
          <div className="bg-gray-800 rounded-lg shadow-xl p-6 text-white h-full overflow-y-auto max-h-[calc(100vh-200px)]">
            <h3 className="text-xl font-bold mb-4 flex items-center gap-2">
              📋 Overlay Manager
            </h3>

            <button
              onClick={() => {
                setEditingOverlay(null);
                setShowEditor(true);
              }}
              className="w-full bg-blue-600 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded-lg mb-4 flex items-center justify-center gap-2 transition"
            >
              <Plus size={18} /> Add Overlay
            </button>

            <div className="space-y-2">
              {overlays.length === 0 ? (
                <p className="text-gray-400 text-sm">No overlays yet. Create one to get started!</p>
              ) : (
                overlays.map((overlay) => (
                  <div
                    key={overlay._id}
                    className="bg-gray-700 p-3 rounded-lg hover:bg-gray-600 transition"
                  >
                    <div className="flex items-center justify-between mb-2">
                      <label className="flex items-center gap-2 flex-1 cursor-pointer">
                        <input
                          type="checkbox"
                          checked={activeOverlays.has(overlay._id)}
                          onChange={() => toggleOverlay(overlay._id)}
                          className="w-4 h-4"
                        />
                        <span className="font-medium text-sm">{overlay.name}</span>
                        <span className="text-xs bg-blue-600 px-2 py-1 rounded">
                          {overlay.type}
                        </span>
                      </label>
                    </div>
                    <div className="flex gap-2">
                      <button
                        onClick={() => {
                          setEditingOverlay(overlay);
                          setShowEditor(true);
                        }}
                        className="flex-1 bg-blue-600 hover:bg-blue-700 text-white text-xs font-bold py-1 px-2 rounded flex items-center justify-center gap-1 transition"
                      >
                        <Edit2 size={14} /> Edit
                      </button>
                      <button
                        onClick={() => deleteOverlay(overlay._id)}
                        className="flex-1 bg-red-600 hover:bg-red-700 text-white text-xs font-bold py-1 px-2 rounded flex items-center justify-center gap-1 transition"
                      >
                        <Trash2 size={14} /> Delete
                      </button>
                    </div>
                  </div>
                ))
              )}
            </div>

            <button
              onClick={() => {
                setIsStreaming(false);
                setStreamError('');
                if (hlsRef.current) {
                  hlsRef.current.destroy();
                  hlsRef.current = null;
                }
              }}
              className="w-full mt-6 bg-gray-700 hover:bg-gray-600 text-white font-bold py-2 px-4 rounded-lg transition"
            >
              ← Back to URL
            </button>
          </div>
        </div>
      </div>

      {/* Overlay Editor Modal */}
      {showEditor && (
        <OverlayEditorModal
          overlay={editingOverlay}
          onClose={() => {
            setShowEditor(false);
            setEditingOverlay(null);
          }}
          onSave={handleSaveOverlay}
        />
      )}
    </div>
  );
}

function OverlayEditorModal({ overlay, onClose, onSave }) {
  const [formData, setFormData] = useState(
    overlay || {
      name: '',
      type: 'text',
      content: '',
      position: { x: 0, y: 0 },
      size: { width: 100, height: 50 },
      zIndex: 1,
      opacity: 1,
    }
  );

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    onSave(formData);
  };

  return (
    <div className="fixed inset-0 bg-black/50 flex items-center justify-center p-4 z-50">
      <div className="bg-gray-800 rounded-lg p-8 max-w-md w-full text-white shadow-2xl max-h-[90vh] overflow-y-auto">
        <div className="flex items-center justify-between mb-6">
          <h2 className="text-2xl font-bold">{overlay ? 'Edit Overlay' : 'Create Overlay'}</h2>
          <button onClick={onClose} className="text-gray-400 hover:text-white transition">
            <X size={24} />
          </button>
        </div>

        <div className="space-y-4">
          <div>
            <label className="block text-sm font-bold mb-2">Overlay Name *</label>
            <input
              type="text"
              name="name"
              value={formData.name}
              onChange={handleChange}
              required
              className="w-full px-3 py-2 bg-gray-700 border border-gray-600 rounded-lg text-white focus:border-blue-500 outline-none"
              placeholder="e.g., Logo, Title"
            />
          </div>

          <div>
            <label className="block text-sm font-bold mb-2">Type *</label>
            <select
              name="type"
              value={formData.type}
              onChange={handleChange}
              className="w-full px-3 py-2 bg-gray-700 border border-gray-600 rounded-lg text-white focus:border-blue-500 outline-none"
            >
              <option value="text">Text</option>
              <option value="shape">Shape</option>
            </select>
          </div>

          <div>
            <label className="block text-sm font-bold mb-2">Content *</label>
            {formData.type === 'text' && (
              <textarea
                name="content"
                value={formData.content}
                onChange={handleChange}
                required
                className="w-full px-3 py-2 bg-gray-700 border border-gray-600 rounded-lg text-white focus:border-blue-500 outline-none"
                placeholder="Enter text"
                rows={3}
              />
            )}
            {formData.type === 'shape' && (
              <input
                type="text"
                name="content"
                value={formData.content}
                onChange={handleChange}
                required
                className="w-full px-3 py-2 bg-gray-700 border border-gray-600 rounded-lg text-white focus:border-blue-500 outline-none"
                placeholder="Color (hex: #FF0000)"
              />
            )}
          </div>

          <div className="grid grid-cols-2 gap-4">
            <div>
              <label className="block text-sm font-bold mb-2">X Position</label>
              <input
                type="number"
                value={formData.position.x}
                onChange={(e) => setFormData({ ...formData, position: { ...formData.position, x: parseInt(e.target.value) || 0 } })}
                className="w-full px-3 py-2 bg-gray-700 border border-gray-600 rounded-lg text-white focus:border-blue-500 outline-none"
                min="0"
              />
            </div>
            <div>
              <label className="block text-sm font-bold mb-2">Y Position</label>
              <input
                type="number"
                value={formData.position.y}
                onChange={(e) => setFormData({ ...formData, position: { ...formData.position, y: parseInt(e.target.value) || 0 } })}
                className="w-full px-3 py-2 bg-gray-700 border border-gray-600 rounded-lg text-white focus:border-blue-500 outline-none"
                min="0"
              />
            </div>
          </div>

          <div className="grid grid-cols-2 gap-4">
            <div>
              <label className="block text-sm font-bold mb-2">Width</label>
              <input
                type="number"
                value={formData.size.width}
                onChange={(e) => setFormData({ ...formData, size: { ...formData.size, width: parseInt(e.target.value) || 10 } })}
                className="w-full px-3 py-2 bg-gray-700 border border-gray-600 rounded-lg text-white focus:border-blue-500 outline-none"
                min="10"
              />
            </div>
            <div>
              <label className="block text-sm font-bold mb-2">Height</label>
              <input
                type="number"
                value={formData.size.height}
                onChange={(e) => setFormData({ ...formData, size: { ...formData.size, height: parseInt(e.target.value) || 10 } })}
                className="w-full px-3 py-2 bg-gray-700 border border-gray-600 rounded-lg text-white focus:border-blue-500 outline-none"
                min="10"
              />
            </div>
          </div>

          <div className="grid grid-cols-2 gap-4">
            <div>
              <label className="block text-sm font-bold mb-2">Z-Index</label>
              <input
                type="number"
                name="zIndex"
                value={formData.zIndex}
                onChange={handleChange}
                className="w-full px-3 py-2 bg-gray-700 border border-gray-600 rounded-lg text-white focus:border-blue-500 outline-none"
                min="0"
              />
            </div>
            <div>
              <label className="block text-sm font-bold mb-2">Opacity (0-1)</label>
              <input
                type="number"
                name="opacity"
                value={formData.opacity}
                onChange={handleChange}
                className="w-full px-3 py-2 bg-gray-700 border border-gray-600 rounded-lg text-white focus:border-blue-500 outline-none"
                min="0"
                max="1"
                step="0.1"
              />
            </div>
          </div>

          <div className="flex gap-3 pt-4">
            <button
              onClick={handleSubmit}
              className="flex-1 bg-blue-600 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded-lg flex items-center justify-center gap-2 transition"
            >
              <Check size={18} /> Save
            </button>
            <button
              onClick={onClose}
              className="flex-1 bg-gray-700 hover:bg-gray-600 text-white font-bold py-2 px-4 rounded-lg transition"
            >
              Cancel
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}